body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color{
  background: #c31432;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #240b36, #c31432);
  background: linear-gradient(to right, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
}

.landing-grid{

  background: #c31432;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #240b36, #c31432);
  background: linear-gradient(to right, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  text-align: center;
position: fixed;
top:0;
left: 0;
width:100%;
height:100%;

}

.projects-grid{
display: flex;
flex-wrap: wrap;
justify-content: space-between;
margin-top: 10px;
background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-linear-gradient(left, #240b36, #c31432);
background: linear-gradient(to right, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
width:100%;
height:100%;
}



.avatar-img{
  height:250px;
  padding-top: 5em;
}








.banner-text h1 {
  font-family: 'Press Start 2P', cursive;
  
  font-size: 4vw;
  
  color:white;
  left: 0;
  line-height: 200px;
  margin: auto;
  margin-top: -100px;
  position: absolute;
  top: 50%;
  width: 100%;
 
}





.social-links{
  display:flex;
  
  left: 0;
  line-height: 200px;
  margin: auto;
  margin-top: 80px;
  position: absolute;
  top: 50%;
  width: 100%;
}

.banner-text i {
color: white;
font-size:4em;
padding: 20px;
margin-top: 80px;

}


.banner-text a{

  text-decoration:none

}
.banner-text p {
  font-size: 20px;
  font-weight: bold;
  color:white;
 
  margin-top: 500px;
  
}

.grid-color{
  background: #c31432;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #240b36, #c31432);
  background: linear-gradient(to right, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  width:100%;
height:100%;
  
} 
.category-tabs{

  background: #c31432;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(left, #240b36, #c31432);
  background: linear-gradient(to right, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
